import React, { useEffect, useState } from "react";
//import React, { useEffect } from "react";
import "./Header.css";
import "./"
import PersonIcon from '@material-ui/icons/Person';
import IconButton from '@material-ui/core/IconButton';
import GetAppIcon from '@material-ui/icons/GetApp';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import { useHistory } from "react-router-dom";
import firebase from "firebase/app"
import 'firebase/analytics';
import database from "./firebase";

let deferredPrompt;


function Header() {

    const [back, setback] = useState(false);
    //const [installable, setInstallable] = useState(false);
    useEffect(() => {

        
        window.addEventListener("beforeinstallprompt", (e) => {
            // Prevent the mini-infobar from appearing on mobile
            e.preventDefault();
            // Stash the event so it can be triggered later.
            deferredPrompt = e;
            // Update UI notify the user they can install the PWA
            //setInstallable(true);
        });

        window.addEventListener('appinstalled', () => {

            firebase.auth().onAuthStateChanged(function (user) {
                if (user) {
                    const Email = firebase.auth().currentUser.email


                    // this.autocat()
                    database.collection("Users").doc(Email).update(
                        {
                            installed: firebase.firestore.FieldValue.arrayUnion(Date().toLocaleString()),

                        }
                    )
                    firebase.analytics().logEvent("INSTALL Withlogin Success")



                }
                else {


                    database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
                        installed: firebase.firestore.FieldValue.arrayUnion(Date().toLocaleString()),
                    })
                    firebase.analytics().logEvent("INSTALL Withoutlogin Success")




                }
            });

        });
        const url = window.location.href.split("/");
        const auth = url[url.length - 2];
        if (auth == "out" || auth == "with") {
            setback(true);
        }
    }, []);

    const handleInstallClick = (e) => {
        // Hide the app provided install promotion
        firebase.analytics().logEvent("Download Clicked")
        // const Email = firebase.auth().currentUser.email

        // database.collection("Users").doc(Email).update(
        //     {
        //         installed: firebase.firestore.FieldValue.arrayUnion(Date().toLocaleString()),

        //     }
        // )
        //setInstallable(false);
        // Show the install prompt
        deferredPrompt.prompt();
        // Wait for the user to respond to the prompt
        deferredPrompt.userChoice.then((choiceResult) => {
            if (choiceResult.outcome === 'accepted') {
                console.log('User accepted the install prompt');
                firebase.analytics().logEvent("User accepted the install prompt")
            } else {
                console.log('User dismissed the install prompt');
                firebase.analytics().logEvent("User dismissed the install prompt")
            }
        });
    };

    const history = useHistory();
    const handlePerson = () => {
        history.push("/profile");
        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                const Email = firebase.auth().currentUser.email


                // this.autocat()
                database.collection("Users").doc(Email).update(
                    {
                        Profile_Click: firebase.firestore.FieldValue.arrayUnion(Date().toLocaleString()),

                    }
                )
                firebase.analytics().logEvent("Profile_Click Withlogin")



            }
            else {


                database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
                    Profile_Click: firebase.firestore.FieldValue.arrayUnion(Date().toLocaleString()),
                })
                firebase.analytics().logEvent("Profile_Click Withoutlogin")




            }
        });
    }
    const handleImg = () => {
        history.push("/");
    }
    const handleBack = () => {
        //history.push("/profile");
        window.open("http://localhost:3000/profile", "_self") //Profile Page
        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                const Email = firebase.auth().currentUser.email


                // this.autocat()
                database.collection("Users").doc(Email).update(
                    {
                        Back_Click: firebase.firestore.FieldValue.arrayUnion(Email),

                    }
                )
                firebase.analytics().logEvent("Back_Click Withlogin")



            }
            else {


                database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
                    Back_Click: firebase.firestore.FieldValue.arrayUnion(Date().toLocaleString()),
                })
                firebase.analytics().logEvent("Back_Click Withoutlogin")




            }
        });
    }


    return (
        <div className="header">


            

            {back==false?(
                <IconButton onClick={handlePerson}>
                    <PersonIcon className="headericon" fontSize="inherit" />
                </IconButton>
            ):(
                <IconButton onClick={handleBack}>
                    <ArrowBackIosIcon className="headericon" fontSize="inherit" />
                </IconButton>
            )}

            <img className="headertind" onClick={handleImg}
                alt="Logo"
                src="/logo.png"
            />


            {/* {installable &&
                <button className="install-button" onClick={handleInstallClick}>
                    INSTALL ME
                </button>
            } */}

            {/* <IconButton onClick={handleMessages}>
                <ChatIcon className="headericon" fontSize="inherit"/>
            </IconButton> */}

            {/* {installable && */}
            <IconButton onClick={handleInstallClick}>
                <GetAppIcon className="headericon" fontSize="inherit" />
            </IconButton>
            {/* } */}
        </div>
    )
}

export default Header