import React from "react";
import database from "./firebase";
import "./Cards.css";
import "./SwipeButtons.css"
import FavoriteIcon from '@material-ui/icons/Favorite';
import CancelIcon from '@material-ui/icons/Cancel';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import IconButton from '@material-ui/core/IconButton';
import FeedbackIcon from '@material-ui/icons/Feedback';
import LanguageIcon from '@material-ui/icons/Language';
import firebase from "firebase/app"
import 'firebase/analytics';
import { Swipeable, direction } from 'react-deck-swiper';
import { Helmet } from "react-helmet";
import ShareIcon from '@material-ui/icons/Share';
import MetaTags from 'react-meta-tags';

class Cards extends React.Component {
    constructor(props) {
        super(props);
        this.withoutlogin = database.collection("CardsWithoutLogin");
        this.withlogin = database.collection("CardsWithLogin").orderBy("sid", "asc");
        this.users = database.collection("Users");
        this.unsubscribe = null;
        this.state = {
            cards: [],
            liked: [],
            disliked: [],
            totalshare: 0,
            totalfeedback: 0,
            totallikes: 0,
            totaldislikes: 0
        };

    }

    componentDidMount() {

        firebase.auth().onAuthStateChanged(user => {
            if (user === null) {
                this.unsubscribe = this.withoutlogin.onSnapshot(this.onCollectionUpdate)

                console.log("Not Signed In!")
            }
            else {
                this.seenCards()
                console.log("liked" + this.state.liked)
                console.log("Disliked" + this.state.disliked)
            }
        })

    }

    cardsanalytics = () => {
        console.log("Data Refreshed!")
        var arry = this.state.cards
        console.log(arry)
        var cardid = arry[arry.length - 1].id
        console.log(cardid)
        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                console.log("In User")
                database.collection("CardsWithLoginAnalytics").where("id", "==", cardid).get()
                    .then(querySnapshot => {
                        //console.log("querySnapshot: ", querySnapshot)
                        let totallikes = 0;
                        let totaldislikes = 0;
                        let totalfeedback = 0;
                        let totalshare = 0;
                        querySnapshot.docs.map(doc => {
                            let { totaldislikes, totallikes, totalfeedback, totalshare } = doc.data()

                            totalshare = totalshare;
                            totalfeedback = totalfeedback;
                            totaldislikes = totaldislikes;
                            totallikes = totallikes;
                            console.log(totalshare)
                            console.log(totalfeedback)
                            console.log(totaldislikes)
                            console.log(totallikes)
                        })

                        this.setState({
                            totallikes: totallikes,
                            totaldislikes: totaldislikes,
                            totalfeedback: totalfeedback,
                            totalshare: totalshare
                        }, () => { console.log("Data Set!") })
                    }).catch((err) => { console.log(err) });
            }
            else {
                console.log("In Without user")
                database.collection("CardsWithoutLoginAnalytics").where("id", "==", cardid).get()
                    .then(querySnapshot => {
                        //console.log("querySnapshot: ", querySnapshot)
                        let totallikes = 0;
                        let totaldislikes = 0;
                        let totalfeedback = 0;
                        let totalshare = 0;
                        querySnapshot.docs.map(doc => {
                            let { totaldislikes, totallikes, totalfeedback, totalshare } = doc.data()

                            totalshare = totalshare;
                            totalfeedback = totalfeedback;
                            totaldislikes = totaldislikes;
                            totallikes = totallikes;
                            console.log(totalshare)
                            console.log(totalfeedback)
                            console.log(totaldislikes)
                            console.log(totallikes)
                        })
                        this.setState({
                            totallikes: totallikes,
                            totaldislikes: totaldislikes,
                            totalfeedback: totalfeedback,
                            totalshare: totalshare
                        }, () => { console.log("Data Set!") })
                    }).catch((err) => { console.log(err) });
            }

        })


    }

    onCollectionUpdate = (querySnapshot) => {
        const cards = [];
        firebase.auth().onAuthStateChanged(user => {
            if (user === null) {
                querySnapshot.forEach((doc) => {
                    const { categories, heading, id, image_links, main_category, ns, pageid, reference, sid, subheading, summary, webpage_url } = doc.data();
                    cards.push({
                        key: doc.id,
                        doc, // DocumentSnapshot
                        categories,
                        heading,
                        id,
                        image_links,
                        main_category,
                        ns,
                        pageid,
                        reference,
                        sid,
                        subheading,
                        summary,
                        webpage_url,
                    });
                })
                this.setState({
                    cards: [...this.state.cards, ...cards]
                });
                console.log("Cards on screen: " + cards.length)
                //this.cardsanalytics()

            } else {
                querySnapshot.forEach((doc) => {
                    const { categories, heading, id, image_links, main_category, ns, pageid, reference, sid, subheading, summary, webpage_url } = doc.data();
                    if (!(this.state.liked.includes(id) || this.state.disliked.includes(id))) {
                        cards.push({
                            key: doc.id,
                            doc, // DocumentSnapshot
                            categories,
                            heading,
                            id,
                            image_links,
                            main_category,
                            ns,
                            pageid,
                            reference,
                            sid,
                            subheading,
                            summary,
                            webpage_url,
                        });
                    }
                })
                //console.log("Cards on screen: "+cards.length)
                this.setState({
                    cards: [...this.state.cards, ...cards.slice(0, 5)]
                });
                console.log("Cards on screen: " + this.state.cards.length)
                //this.cardsanalytics()

            }
        })
    }


    seenCards = () => {
        console.log("Cards Refreshed!")
        const email = firebase.auth().currentUser.email
        console.log("User Available: " + email)
        database.collection("Users").where("Email", "==", email)
            .get()
            .then(querySnapshot => {
                //console.log("querySnapshot: ", querySnapshot)
                let likedCards = []
                let dislikedCards = []
                querySnapshot.docs.map(doc => {
                    const { Name, disliked, liked } = doc.data()
                    console.log("User Name: " + Name); // array of cities objects
                    console.log("Liked Cards: " + liked); // array of cities objects
                    console.log("Disliked Cards: " + disliked); // array of cities objects
                    likedCards = [...likedCards, ...(liked || [])]
                    dislikedCards = [...dislikedCards, ...(disliked || [])]
                    return;
                })
                this.setState({
                    liked: [...this.state.liked, ...likedCards],
                    disliked: [...this.state.disliked, ...dislikedCards]
                }, () => { this.unsubscribe = this.withlogin.onSnapshot(this.onCollectionUpdate) })
            }).catch((err) => { console.log(err) });
    }

    swiped = (swipeDirection) => {
        var arry = this.state.cards
        var cardid = arry[arry.length - 1].id

        console.log(cardid + " swiped " + swipeDirection)

        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                const Email = firebase.auth().currentUser.email

                if (swipeDirection === direction.RIGHT) {
                    database.collection("Users").doc(Email).update(
                        {
                            liked: firebase.firestore.FieldValue.arrayUnion(cardid),
                            totalliked: firebase.firestore.FieldValue.increment(1)

                        }
                    )
                    database.collection("CardsWithLoginAnalytics").doc(cardid).set(
                        {
                            id: cardid,
                            likes: firebase.firestore.FieldValue.arrayUnion(Email),
                            totallikes: firebase.firestore.FieldValue.increment(1)

                        }, { merge: true })

                    firebase.analytics().logEvent("Liked WithLogin")

                }
                if (swipeDirection === direction.LEFT) {
                    database.collection("Users").doc(Email).update(
                        {
                            disliked: firebase.firestore.FieldValue.arrayUnion(cardid),
                            totaldisliked: firebase.firestore.FieldValue.increment(1)
                        }
                    )
                    database.collection("CardsWithLoginAnalytics").doc(cardid).set(
                        {
                            id: cardid,
                            likes: firebase.firestore.FieldValue.arrayUnion(Email),
                            totallikes: firebase.firestore.FieldValue.increment(1)

                        }, { merge: true })
                    firebase.analytics().logEvent("DisLiked WithLogin")

                }

            }
            else {

                if (swipeDirection === direction.RIGHT) {
                    database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
                        totalliked: firebase.firestore.FieldValue.increment(1),
                    })
                    database.collection("CardsWithoutLoginAnalytics").doc(cardid).set(
                        {
                            id: cardid,
                            totallikes: firebase.firestore.FieldValue.increment(1)

                        }, { merge: true })
                    firebase.analytics().logEvent("Liked WithoutLogin")

                }
                if (swipeDirection === direction.LEFT) {
                    database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
                        totaldisliked: firebase.firestore.FieldValue.increment(1),
                    })
                    database.collection("CardsWithoutLoginAnalytics").doc(cardid).set(
                        {
                            id: cardid,
                            totaldislikes: firebase.firestore.FieldValue.increment(1)

                        }, { merge: true })
                    firebase.analytics().logEvent("DisLiked WithoutLogin")
                }

            }


        })
        arry.pop()
        this.setState({ cards: arry })
        if (this.state.cards.length === 0) {
            console.log("line no: 211")

            firebase.auth().onAuthStateChanged(user => {
                if (user === null) {
                    window.open(window.location.href + "profile", "_self") //Profile Page
                }
                else {
                    this.seenCards() //Card refresh
                }
            })
        }
    }



    feedback = () => {
        var arry = this.state.cards
        var cardid = arry[arry.length - 1].id


        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                const Email = firebase.auth().currentUser.email
                database.collection("Users").doc(Email).update(
                    {
                        feedback: firebase.firestore.FieldValue.increment(1)
                    }
                )
                database.collection("CardsWithLoginAnalytics").doc(cardid).set(
                    {
                        feedbacks: firebase.firestore.FieldValue.arrayUnion(Email),
                        totalfeedbacks: firebase.firestore.FieldValue.increment(1)

                    }, { merge: true })
                console.log(cardid + " feedback ")

                firebase.analytics().logEvent("Feedback WithLogin")
            }
            else {
                database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
                    feedback: firebase.firestore.FieldValue.increment(1)
                })
                database.collection("CardsWithoutLoginAnalytics").doc(cardid).set(
                    {
                        totalfeedbacks: firebase.firestore.FieldValue.increment(1)

                    }, { merge: true })
                console.log(cardid + " feedback ")
                firebase.analytics().logEvent("Feedback WithoutLogin")
            }
        })
    }

    handleShare = () => {
        var arry = this.state.cards
        var cardheading = arry[arry.length - 1].heading
        var cardsubheading = arry[arry.length - 1].subheading
        var cardsummary = arry[arry.length - 1].summary
        var cardid = arry[arry.length - 1].id

        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                const Email = firebase.auth().currentUser.email
                if (navigator.share) {
                    const text = "*" + cardheading + "*" + "%0a```" + cardsubheading + "```%0a" + "%0a_" + cardsummary + "_" + "%0a*Visit:* https://swipeekaro.web.app/card/with/" + cardid
                    window.open("whatsapp://send?text=" + text)
                }
                database.collection("Users").doc(Email).update(
                    {
                        share: firebase.firestore.FieldValue.increment(1)
                    }
                )
                database.collection("CardsWithLoginAnalytics").doc(cardid).set(
                    {
                        shares: firebase.firestore.FieldValue.arrayUnion(Email),
                        totalshares: firebase.firestore.FieldValue.increment(1)

                    }, { merge: true })
                firebase.analytics().logEvent("Share WithLogin")
            }
            else {
                if (navigator.share) {
                    const text = "*" + cardheading + "*" + "%0a```" + cardsubheading + "```%0a" + "%0a_" + cardsummary + "_" + "%0a*Visit:* https://swipeekaro.web.app/card/out/" + cardid
                    window.open("whatsapp://send?text=" + text)
                }
                database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
                    share: firebase.firestore.FieldValue.increment(1)
                })
                database.collection("CardsWithoutLoginAnalytics").doc(cardid).set(
                    {
                        totalshares: firebase.firestore.FieldValue.increment(1)

                    }, { merge: true })
                firebase.analytics().logEvent("Share WithoutLogin")
            }
        })
    };

    handleWeb = () => {
        var arry = this.state.cards
        var web = arry[arry.length - 1].webpage_url
        var cardid = arry[arry.length - 1].id
        console.log(web)
        window.open(web, "_blank")


        firebase.auth().onAuthStateChanged(function (user) {
            if (user) {
                const Email = firebase.auth().currentUser.email
                database.collection("Users").doc(Email).update(
                    {
                        wikiopen: firebase.firestore.FieldValue.increment(1)
                    }
                )
                database.collection("CardsWithLoginAnalytics").doc(cardid).set(
                    {
                        wikiopen: firebase.firestore.FieldValue.arrayUnion(Email),
                        totalwikiopen: firebase.firestore.FieldValue.increment(1)

                    }, { merge: true })
                firebase.analytics().logEvent("Share WithLogin")
            }
            else {
                database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
                    share: firebase.firestore.FieldValue.increment(1)
                })
                database.collection("CardsWithoutLoginAnalytics").doc(cardid).set(
                    {
                        totalwikiopen: firebase.firestore.FieldValue.increment(1)

                    }, { merge: true })
                firebase.analytics().logEvent("Share WithoutLogin")
            }
        })
    };
    convert = (value) => {
        if (isNaN(value)) return value;

        if (value < 9999) {
            return value;
        }

        if (value < 1000000) {
            return Math.round(value / 1000) + "K";
        }
        if (value < 10000000) {
            return (value / 1000000).toFixed(2) + "M";
        }

        if (value < 1000000000) {
            return Math.round((value / 1000000)) + "M";
        }

        if (value < 1000000000000) {
            return Math.round((value / 1000000000)) + "B";
        }

    }

    render() {
        return (
            <div className="App">
                {this.state.cards.map((cards) =>
                    <div key={cards.id} id={cards.id} >
                        {/* <Helmet>
                            <meta charSet="utf-8" />
                            <title>{cards.heading}</title>
                            <meta name="description" content={cards.subheading} />
                        </Helmet> */}
                        <MetaTags>
                            <title>{cards.heading}</title>
                            <meta name="description" content={cards.subheading} />
                        </MetaTags>
                        <Swipeable
                            onSwipe={this.swiped}
                            renderButtons={({ left, right }) => (
                                <div className="btn">
                                    <div className="swipeButtons">
                                        <IconButton className="swipeButtons_rep" href="https://docs.google.com/forms/u/2/d/e/1FAIpQLSdzFp8LdIP4cOHMs6mMSXXALHm1n-DXo1HHxQPBM8SYX96n7Q/viewform?embedded=true" onClick={this.feedback} >
                                            <FeedbackIcon fontSize="large" />
                                        </IconButton>

                                        <IconButton className="swipeButtons_canc" onClick={left}>
                                            <CancelIcon fontSize="large" />
                                        </IconButton>
                                        <IconButton className="swipeButtons_web" onClick={this.handleWeb} >
                                            <LanguageIcon fontSize="large" />
                                        </IconButton>
                                        <IconButton className="swipeButtons_fav" onClick={right} >
                                            <FavoriteIcon fontSize="large" />
                                        </IconButton>
                                        <IconButton className="swipeButtons_what" onClick={this.handleShare} data-action="share/whatsapp/share">
                                            <ShareIcon fontSize="large" />
                                        </IconButton>
                                    </div>
                                </div>
                            )}
                        >
                            <div className="card"  >
                                <div className="buttonFalse">
                                    <img src={cards.image_links[0]} alt={cards.heading} />
                                </div>
                                <div className="post-content">
                                    <div className="category"><mark className="highlight"> {"  #"+cards.main_category+"  "} </mark></div>
                                    <h1 className="title">{cards.heading}</h1>
                                    <div className="sub_title">
                                        <p>{cards.subheading}</p>
                                    </div>
                                    {/* <div className="cardanalytics">

                                        <div className="analytics">
                                            <div className="like">

                                                <FavoriteIcon className="swipeButtons_fav" fontSize="small" />
                                                <h3>{this.convert(this.state.totallikes)}</h3>
                                            </div>
                                            <div className="dislike">
                                                <CancelIcon className="swipeButtons_canc" fontSize="small" />
                                                <h3>{this.convert(this.state.totaldislikes)}</h3>
                                            </div>
                                            <div className="whatsapp">
                                                <ShareIcon className="swipeButtons_what" fontSize="small" />
                                                <h3>{this.convert(this.state.totalshare)}</h3>
                                            </div>
                                            <div className="feedback">
                                                <FeedbackIcon className="swipeButtons_rep" fontSize="small" />
                                                <h3>{this.convert(this.state.totalfeedback)}</h3>
                                            </div>
                                        </div>

                                    </div> */}
                                    <hr className="solid"></hr>

                                    <p className="descriptionn">{cards.summary.replace(/@n/gi, '\n\n')}<br /><br /><b>{cards.reference}</b></p>

                                </div>
                            </div>

                        </Swipeable>

                    </div>
                )}
            </div>
        );
    }
}

export default Cards;