import "./Profile.css";
import "./"
import React, { Component } from "react"
import firebase from "firebase"
import StyledFirebaseAuth from "react-firebaseui/StyledFirebaseAuth"
import database from "./firebase";
import { Helmet } from "react-helmet";
import { Grid } from "@material-ui/core";
import FavoriteIcon from '@material-ui/icons/Favorite';
import CancelIcon from '@material-ui/icons/Cancel';
import FeedbackIcon from '@material-ui/icons/Feedback';
import ShareIcon from '@material-ui/icons/Share';


class Profile extends Component {
  state = {
    isSignedIn: false,
    liked: 0,
    disliked: 0,
    totalshare: 0,
    totalfeedback: 0
  }
  uiConfig = {
    signInFlow: "popup",
    signInOptions: [
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,
      // firebase.auth.FacebookAuthProvider.PROVIDER_ID,
      // firebase.auth.TwitterAuthProvider.PROVIDER_ID,
      firebase.auth.EmailAuthProvider.PROVIDER_ID,

    ],
    callbacks: {
      signInSuccess: () => false
    }
  }

  users = () => {
    console.log("Cards Refreshed!")
    const email = firebase.auth().currentUser.email
    console.log("User Available: " + email)
    database.collection("Users").where("Email", "==", email)
      .get()
      .then(querySnapshot => {
        //console.log("querySnapshot: ", querySnapshot)
        let likedCards;
        let dislikedCards;
        let totalfeedback;
        let totalshare;
        querySnapshot.docs.map(doc => {
          const { Name, totaldisliked, totalliked, feedback, share } = doc.data()
          console.log("User Name: " + Name); // array of cities objects
          console.log("Liked Cards: " + totalliked); // array of cities objects
          console.log("Disliked Cards: " + totaldisliked); // array of cities objects
          likedCards = totalliked;
          dislikedCards = totaldisliked;
          totalfeedback = feedback;
          totalshare = share;
          return;
        })
        this.setState({
          liked: likedCards,
          disliked: dislikedCards,
          totalshare: totalshare,
          totalfeedback: totalfeedback
        }, () => { console.log("Data Set!") })
      }).catch((err) => { console.log(err) });
  }
  handleShare = () => {
    if (navigator.share) {
      const text = "*Wisdom is knowing what you don't know - Socrates* Use Jano app to refresh and discover new topics. Quickly read about them and update your knowledge in idle time. *Visit:* https://janoocards.web.app/"
      window.open("whatsapp://send?text=" + text)
    }
    firebase.auth().onAuthStateChanged(function (user) {
      if (user) {
        const Email = firebase.auth().currentUser.email
        database.collection("Users").doc(Email).update(
          {
            Profile_share: firebase.firestore.FieldValue.increment(1)
          }
        )
        firebase.analytics().logEvent("Profile_share WithLogin")
      }
      else {
        database.collection("WithoutLoginAnalytics").doc("WithoutLogin").update({
          Profile_share: firebase.firestore.FieldValue.increment(1)
        })
        firebase.analytics().logEvent("Profile_share WithoutLogin")
      }
    })
  };

  createDoc = () => {
    const Name = firebase.auth().currentUser.displayName
    const Email = firebase.auth().currentUser.email
    database.collection("Users").doc(Email).set({
      Name: Name,
      Email: Email,
      // liked: [],
      // disliked: [],
      // totalliked: 0,
      // totaldisliked: 0,

    }, { merge: true })
    console.log("New Document Created. ")
    this.users()
  }
  convert = (value) => {
    if (isNaN(value)) return value;

    if (value < 9999) {
      return value;
    }

    if (value < 1000000) {
      return Math.round(value / 1000) + "K";
    }
    if (value < 10000000) {
      return (value / 1000000).toFixed(2) + "M";
    }

    if (value < 1000000000) {
      return Math.round((value / 1000000)) + "M";
    }

    if (value < 1000000000000) {
      return Math.round((value / 1000000000)) + "B";
    }

  }

  componentDidMount = () => {

    firebase.auth().onAuthStateChanged(user => {
      this.setState({ isSignedIn: !!user })
      console.log("user" + user)
    })

    firebase.auth().onAuthStateChanged(user => {

      if (user == null) {
        console.log("Inside Function user null");
      }
      else {
        this.createDoc()
      }
    })





  }


  render() {
    return (

      <div className="App">
        <Helmet>
          <title>Profile</title>
          <meta name="description" content="JanooCards Profile" />
        </Helmet>
        {this.state.isSignedIn ? (

          <div className="tinderCards_cardContainer">

            <div className="cardlogin">
              <div class="imgcontainer">


                {firebase.auth().currentUser.photoURL === null ? (
                  <img src="/logo.png" alt="Avatar" height="100px" class="avatar"></img>
                )
                  : (
                    <img src={firebase.auth().currentUser.photoURL} alt="Avatar" height="100px" class="avatar"></img>
                  )}

              </div>


              <h1 class="name"> Hi, {firebase.auth().currentUser.displayName}</h1>
              <div class="signedintext">

                You are signed in!

                <br />
                
                <div className="analytics">
                  <div className="like">

                    <FavoriteIcon className="swipeButtons_fav" fontSize="large" />
                    <h3>{this.convert(this.state.liked)}</h3>
                  </div>
                  <div className="dislike">
                    <CancelIcon className="swipeButtons_canc" fontSize="large" />
                    <h3>{this.convert(this.state.disliked)}</h3>
                  </div>
                  <div className="whatsapp">
                    <ShareIcon className="swipeButtons_what" fontSize="large" />
                    <h3>{this.convert(this.state.totalshare)}</h3>
                  </div>
                  <div className="feedback">
                    <FeedbackIcon className="swipeButtons_rep" fontSize="large" />
                    <h3>{this.convert(this.state.totalfeedback)}</h3>
                  </div>
                </div>

               
                <br />

                <h2 class="email"> If you liked the experience so far, please share it in your network and help us reach more learners. </h2>
                <button class="buttonn" onClick={() => firebase.auth().signOut()}><b>SIGNOUT</b></button>
                <button class="share" onClick={this.handleShare} data-action="share/whatsapp/share"><b>INVITE</b></button>
              </div>
              {/* <h1 class="name"> {firebase.auth().currentUser.uid}</h1> */}
              {/* <h2 class="email"> {firebase.auth().currentUser.email}</h2> */}


            </div>



          </div>
        )
          : (
            <div className="tinderCards_cardContainer">

              <div className="cardlogin">
                <div class="imgcontainer">
                  <img src="/logo.png" alt="Avatar" height="100px" class="avatar"></img>
                </div>
                <div class="signintext">Sign-in to unlock full version !
                </div>
                <StyledFirebaseAuth
                  uiConfig={this.uiConfig}
                  firebaseAuth={firebase.auth()}
                />

              </div>


            </div>
          )}

      </div>
    )
  }
}

export default Profile